export default {
	data() {
		return {
			// 所有收货地址列表
			addressList: [],
			address_id: '',
			// 默认选中项
			// current: '',
			user_id: ''
		}
	},
	onLoad(options) {
		this.user_id = uni.getStorageSync('user_id')
		// 查询所有收货地址接口
		this.getAllAddress()
	},
	methods: {
		// 去新建地址页面
		toGreenAddrManageAdd() {
			uni.navigateTo({
				url: '/pages/greenAddrManageAdd/greenAddrManageAdd'
			})
		},
		// 根据 id 删除地址
		del(id) {
			uni.showModal({
					content: '确定删除该地址?',
					success: res => {
						if (res.confirm) {
								this.$http.get('/user/delAddressById', {
									params: {
										id: id
									}
								}).then(res => {
									this.getAllAddress()
								})
						} else if (res.cancel) {
								console.log('用户点击取消');
						}
					}
			});
		},
		// 去确认订单页面
		goConfirmOrder(id) {
			uni.navigateTo({
				url: '/pages/confirmOrder/confirmOrder?id=' + id
			})
		},
		// 去编辑地址页面
		goGreenAddrManageAdd(id) {
			uni.navigateTo({
				url: '/pages/greenAddrManageAdd/greenAddrManageAdd?id=' + id
			})
		},
		// 查询所有收货地址接口
		getAllAddress() {
			this.$http.get('/user/getAllAddress', {
				params: {
					userId: this.user_id
				}
			}).then(res => {
				console.log(res)
				this.addressList = res.data
				// console.log(this.addressList)
			})
		},
		// 当默认地址 radio 框发生变化的时候
		radioChange(e) {
			console.log(e.detail.value)
			this.address_id = e.detail.value
			// this.address_id = e.detail.value
			// this.current = e.detail.value
			this.$http.get('/user/updateDefaultAddress', { params: { user_id: this.user_id, address_id: this.address_id } }).then(res => {
				console.log(e)
			// 	// if (res.data = 'success') {
			// 	// 	uni.showToast({
			// 	// 			title: '更改成功',
			// 	// 			duration: 2000,
			// 	// 			icon: 'none'
			// 	// 	});
			// 	// }
			})
		}
	}
}
